/**
 * Home page scripts.
 *
 * @module Home
 */

import { $window, css, isScrolledIntoView, Resp } from '../modules/dev/_helpers'
import { Linear, Power1, Power4, TimelineMax } from 'gsap'

export default class Home {
  /**
   * Cache data, make preparations and initialize page scripts.
   */
  constructor () {
    if (Resp.isMobile) return
    this.section = 'section'

    this.firstTween = new TimelineMax()
    this.firstScreen = '.first_screen_new'

    this.secondScreen = '.clients__screen'
    this.secondTween = new TimelineMax().set([`${this.secondScreen} h3`, `${this.secondScreen} .content__block`], {opacity: 0})

    this.contetItems = '.clients__items > div'

    this.thirdScreen = '.products__screen'
    this.thirdTween = new TimelineMax().set([`${this.thirdScreen}`], {opacity: 0})

    this.thirdScreenContentItmes = '.products__list > .product__item'

    this.fourthScreen = '.trustworthy'
    this.fourthTween = new TimelineMax().set([`${this.fourthScreen}`], {opacity: 0})

    this.fourthContentItems = '.items'

    this.sliderScreen = '.slider_screen'
    this.sliderTween = new TimelineMax().set([`${this.sliderScreen}`], {opacity: 0})

    this.newsScreen = '.news_block'
    this.newsTween = new TimelineMax().set([`${this.newsScreen}`], {opacity: 0})

    this.ctaScreen = '.cta_block_'
    this.ctaTween = new TimelineMax().set([`${this.ctaScreen}`], {opacity: 0})

    this.init()
  }

  /**
   * Initialize Home page scripts.
   */
  init () {
    let _this = this

    this.initFirstScreen()

    $window.on('scroll', function (e) {
      $(_this.section).each(function () {
        let index = $(_this.section).index(this)

        if (isScrolledIntoView($(_this.secondScreen))) {
          let index = $(_this.section).index(this)
          if (isScrolledIntoView($(this)) && index === 1) {
            _this.initSecondScreen()
          }
        }
        if (isScrolledIntoView($(_this.thirdScreen), -50)) {
          if (isScrolledIntoView($(this)) && index === 2) {
            _this.initThirdScreen()
          }
        }
        if (isScrolledIntoView($(_this.newsScreen))) {
          _this.initNewsScreen()
        }

        if (isScrolledIntoView($(_this.ctaScreen))) {
          if (isScrolledIntoView($(this))) {
            _this.initCtaScreen()
          }
        }
      })
    })

    let $leftButton = $('.left_button')
    let $rightButton = $('.right_button')
    let $slides = $('.slide')

    // $rightButton.click(function () {
    //   let index = $('.slide.active').index()
    //
    //   console.log('here right')
    //
    //   // new TimelineMax().fromTo('.slide.active', .6, {y: 0, opacity: 1}, {
    //   //   y: -20,
    //   //   opacity: 0,
    //   //   ease: Power1.easeOut,
    //   // }, .55)
    //   //
    //   // new TimelineMax().fromTo($(`.slide:eq(${index + 1})`), .6, {y: -20, opacity: 0}, {
    //   //   y: 0,
    //   //   opacity: 1,
    //   //   ease: Power1.easeOut,
    //   // }, .55)
    //
    //   $slides.removeClass(css.active)
    //   $(`.slide:eq(${index + 1})`).addClass(css.active)
    //
    // })
  }

  initFirstScreen () {
    this.firstTween
    .fromTo(`header`, .7, {y: -120, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1)
    .fromTo(`${this.firstScreen} h1`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.75)
    .fromTo(`${this.firstScreen} p`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 2)
    .fromTo(`${this.firstScreen} .first__bg`, .8, {x: 220, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 2.5)
    .fromTo(`${this.firstScreen} .left__side`, 2, {x: 220, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 3)
    .fromTo(`${this.firstScreen} .left__side_1`, 2.3, {y: -30, x: -50, opacity: 0}, {
      y: 0,
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 3.2)
    .fromTo(`${this.firstScreen} .left__side_2`, 2, {y: -30, x: 50, opacity: 0}, {
      y: 0,
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 3.3)
    .fromTo(`${this.firstScreen} .second__bg`, .8, {x: -220, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.35)
    .fromTo(`${this.firstScreen} .ellipse_1`, 1.5, {opacity: 0, ease: Power1.easeOut}, {
      opacity: 1,
      ease: Power1.easeOut
    }, 2.1)
    .fromTo(`${this.firstScreen} .ellipse_2`, 2, {x: 45, opacity: 0, ease: Power1.easeOut}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut
    }, 1.8)
    .fromTo(`${this.firstScreen} .ellipse_3`, 1.5, {x: -45, y: 50, opacity: 0, ease: Power1.easeOut}, {
      x: 0,
      y: 0,
      opacity: 1,
      ease: Power1.easeOut
    }, 2.1)
    .fromTo(`${this.firstScreen} .ellipse_5`, 1.5, {x: -45, y: 50, opacity: 0, ease: Power1.easeOut}, {
      x: 0,
      y: 0,
      opacity: 1,
      ease: Power1.easeOut
    }, 2.1)
    .fromTo(`${this.firstScreen} .ellipse_7`, 1.5, {x: -45, y: 50, opacity: 0, ease: Power1.easeOut}, {
      x: 0,
      y: 0,
      opacity: 1,
      ease: Power1.easeOut
    }, 2.1)
    .fromTo(`${this.firstScreen} .ellipse_6`, 1.5, {opacity: 0, ease: Power1.easeOut}, {
      opacity: 1,
      ease: Power1.easeOut
    }, 2.1)
    .fromTo(`${this.firstScreen} .ellipse_4`, 2.3, {x: 85, opacity: 0, ease: Power1.easeOut}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut
    }, 2)
    .fromTo(`${this.firstScreen} .row`, .9, {y: 30, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power4.easeOut,
    }, 1.6)
    .fromTo(`${this.firstScreen} .btn`, .9, {y: 50, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.7)

    this.initFirstScreenParallax()
  }

  initSecondScreen () {
    if ($(`${this.secondScreen}`).hasClass(css.finished) || this.secondTween.isActive()) return

    this.secondTween
    .to([`${this.secondScreen} .content__block`], .8, {opacity: 1, ease: Power1.easeOut}, .5)
    .fromTo(`${this.secondScreen} h3`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.4)
    .staggerFromTo(this.contetItems, 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.2, 1.8)
    .addCallback(() => $(this.secondScreen).addClass(css.finished))
  }

  initThirdScreen () {
    if ($(`${this.thirdScreen}`).hasClass(css.finished) || this.thirdTween.isActive()) return

    this.initThirdScreenParallax()

    this.thirdTween
    .to(`${this.thirdScreen}`, .8, {opacity: 1, ease: Power1.easeOut}, .5)
    .fromTo(`${this.thirdScreen} h4`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.4)
    .addCallback(() => {
      $(this.thirdScreen).addClass(css.finished)
    })

    let _this = this
    $window.on('scroll', function (e) {
      $(_this.thirdScreenContentItmes).each(function () {
        let index = $(_this.thirdScreenContentItmes).index(this)
        let selector = `.products__list > .product__item:eq(${index})`
        let $item = $(selector)
        if (isScrolledIntoView($item) && !$item.hasClass(css.active)) {
          $item.addClass(css.active)
          let timeLine = new TimelineMax()
          timeLine.fromTo(`${selector}.${css.active}  .sub__info`, .7, {y: -30, opacity: 0}, {
            y: 0,
            opacity: 1,
            ease: Power1.easeOut,
          }, .55)
          .fromTo(`${selector}.${css.active}  .sub_title`, .7, {y: -20, opacity: 0}, {
            y: 0,
            opacity: 1,
            ease: Power1.easeOut,
          }, .75)
          .fromTo(`${selector}.${css.active}  .sub__descr`, .6, {y: -20, opacity: 0}, {
            y: 0,
            opacity: 1,
            ease: Power1.easeOut,
          }, 1)
          .fromTo(`${selector}.${css.active}  .btn`, .6, {y: -20, opacity: 0}, {
            y: 0,
            opacity: 1,
            ease: Power1.easeOut,
          }, .9)

          if ($item.hasClass('reverse')) {
            timeLine.fromTo(`${selector}.${css.active}  .left_side > img`, .6, {x: -120, opacity: 0}, {
              x: 0,
              opacity: 1,
              ease: Power1.easeOut,
            }, .9)
          } else {
            timeLine.fromTo(`${selector}.${css.active}  .right_side > img`, .6, {x: 120, opacity: 0}, {
              x: 0,
              opacity: 1,
              ease: Power1.easeOut,
            }, .9)
          }
        }
      })
    })
  }

  initThirdScreenParallax () {
    let $screen = $(this.thirdScreen)
    let $parallaxItems = $screen.find('.parallax')
    let _this = this

    $screen.on('mousemove', (event) => {
      let pageX = event.pageX - ($screen.width() * 0.5)
      let pageY = event.pageY - ($screen.height() * 0.5)

      $parallaxItems.each(function () {
        let item = $(this)
        _this.setParallax(item, pageX, 50, pageY, 15)
      })
    })
  }

  initSliderScreen () {
    if ($(`${this.sliderScreen}`).hasClass(css.finished) || this.sliderTween.isActive()) return

    this.sliderTween
    .to(`${this.sliderScreen}`, .1, {opacity: 1, ease: Power1.easeOut}, .3)
    .fromTo(`${this.sliderScreen} h1`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .3)
    .fromTo($(`${this.sliderScreen} .slide:eq(0)`), .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .55)
    .staggerFromTo(`${this.sliderScreen} .slide  div`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 0.2, 1)
    .addCallback(() => {
      $(this.sliderScreen).addClass(css.finished)
    })

  }

  initNewsScreen () {
    if ($(`${this.newsScreen}`).hasClass(css.finished) || this.newsTween.isActive()) return

    this.newsTween
    .to(`${this.newsScreen}`, .1, {opacity: 1, ease: Power1.easeOut}, .3)
    .fromTo(`${this.newsScreen} h1`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .85)
    .fromTo(`${this.newsScreen} p`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1)
    .staggerFromTo(`${this.newsScreen}  .news_item`, .9, {y: -70, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 0.2, 1.4)
    .fromTo(`${this.newsScreen} .btn`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 2)
    .addCallback(() => {
      $(this.newsScreen).addClass(css.finished)
    })
  }

  initCtaScreen () {
    if ($(`${this.ctaScreen}`).hasClass(css.finished) || this.ctaTween.isActive()) return

    this.initCtaParallax()

    this.ctaTween
    .to(`${this.ctaScreen}`, .1, {opacity: 1, ease: Power1.easeOut}, .3)
    .fromTo(`${this.ctaScreen} h2`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .85)
    .fromTo(`${this.ctaScreen} p`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.2)
    .fromTo(`${this.ctaScreen} .row`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.4)
    .fromTo(`${this.ctaScreen} .line_arrows`, .6, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .7)
    .fromTo(`${this.ctaScreen} .btn`, .5, {y: 60, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.2)
    .fromTo(`${this.ctaScreen} .cta`, .9, {y: 120, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .7)
    .addCallback(() => {
      $(this.ctaScreen).addClass(css.finished)
    })

    new TimelineMax({delay: .9, repeat: -1}).staggerFromTo(`${this.ctaScreen} .items`, .75, {y: 20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .8)
    .addCallback(() => {
      $(this.ctaScreen).addClass(css.finished)
    })

    new TimelineMax({
      repeat: -1
    }).staggerFromTo(`${this.ctaScreen} .nav0`, .3, {opacity: 0, ease: Power1.easeOut}, {
      opacity: 1,
      ease: Power1.easeOut,
    }, 0.2)

  }

  initCtaParallax () {
    let $screen = $(this.ctaScreen)
    let $parallaxItems = $screen.find('.parallax')
    let _this = this

    $screen.on('mousemove', (event) => {
      let pageX = event.pageX - ($screen.width() * 0.5)
      let pageY = event.pageY - ($screen.height() * 0.5)

      $parallaxItems.each(function () {
        let item = $(this)
        _this.setParallax(item, pageX, 50, pageY, 10)
      })
    })
  }

  initFirstScreenParallax () {
    let $screen = $(`${this.firstScreen}`)
    let $parallaxItems = $screen.find('.parallax')

    let _thus = this

    $screen.on('mousemove', (event) => {
      let pageX = event.pageX - ($screen.width() * 0.5)
      let pageY = event.pageY - ($screen.height() * 0.5)

      $parallaxItems.each(function () {
        let item = $(this)

        if (item.hasClass('left__side')) {
          _thus.setParallax(item, pageX, 50, pageY, -55)
        } else if (item.hasClass('left__side_1')) {
          _thus.setParallax(item, pageX, -65, pageY, 85)
        } else if (item.hasClass('left__side_2')) {
          _thus.setParallax(item, pageX, 25, pageY, -25)
        } else if (item.hasClass('ellipse_3')) {
          _thus.setParallax(item, pageX, 65, pageY, 85)
        } else if (item.hasClass('ellipse_5')) {
          _thus.setParallax(item, pageX, -65, pageY, -35)
        } else if (item.hasClass('ellipse_2')) {
          _thus.setParallax(item, pageX, 25, pageY, -35)
        } else if (item.hasClass('chair')) {
          _thus.setParallax(item, pageX, -75, pageY, 35)
        } else {
          _thus.setParallax(item, pageX, 50, pageY, 75)
        }
      })
    })
  }

  setParallax (item, pageX, speedX, pageY, speedY) {
    TweenLite.to(item, 0.5, {
      x: (item.position().left + pageX * speedX) * 0.0008,
      y: (item.position().top + pageY * speedY) * 0.0008,
      ease: Linear.ease
    })
  }
}



