import { Linear, Power1, TimelineMax, TweenLite, } from 'gsap'
import { $document, $window, css, isScrolledIntoView } from '../modules/dev/_helpers';

export default class Services {
  constructor () {
    this.firstTween = new TimelineMax()
    this.firstScreen = '.firstScreen'
    this.section = 'section'

    this.contetItems = '.service__wrapper  .service__items'

    this.init()
  }

  init () {
    let _this = this

    this.initFirstScreen()
    this.initWords()

  }

  initFirstScreen() {
    this.firstTween
    .fromTo(`header`, .7, { y: -120, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.5)
    .fromTo(`${this.firstScreen} h3`, .7, { y: -20, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.95)
    .staggerFromTo(this.contetItems, 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.4, 2.5)
    .addCallback(() => $(this.secondScreen).addClass(css.finished));
  }

  initWords () {
    let $headTitle = $('.head__title')
    let sliderSpan = $headTitle.find('p:eq(1)')

    let $childrens = sliderSpan.children()

    $childrens.each(function (index) {
      let $this = $(this)

      if (!index) {
        $this.addClass('is-showing').addClass('show-on-load')
      } else {
        $this.addClass('is-hiding').addClass('hide-on-load')
      }
    })
    
    $document.ready(function (e) {
      let t = 250, n = 2500, r = n - 3e3

      function o (e) {return e.is(':last-child') ? e.parent().children().eq(0) : e.next()}

      setTimeout(function () {
        let i = $('.head__title')
        let a = n

        i.each(function () {
          let i = e(this), s = i.find('.is-showing').outerWidth(), c = t - 100
          setTimeout(function () {
            i.find('p:eq(1)').css('width', s)
          }, c),
            setTimeout(function () {
              i.find('p:eq(1)').addClass('is-loading')
            }, r),
            setTimeout(function () {
              !function e (t) {
                let i, a, s = o(t), c = o(t).outerWidth()
                t.parents('p').removeClass('is-loading'), a = s, (i = t).hasClass('show-on-load') && i.removeClass('show-on-load'), a.hasClass('hide-on-load') && a.removeClass('hide-on-load'), i.removeClass('is-showing').addClass('is-hiding'), a.removeClass('is-hiding').addClass('is-showing'),
                  setTimeout(function () {e(s)}, n),
                  setTimeout(function () {t.parents('p').addClass('is-loading')}, r), t.parents('p').css('width', c)
              }(i.find('.is-showing').eq(0))
            }, a)
        })
      }, 2000)
    })
  }
}



