import { Power1, TimelineMax } from 'gsap'

export default class Article {
  constructor () {
    this.section = 'section'

    this.firstScreen = '.head__screen'
    this.firstTween = new TimelineMax({delay: .9})

    this.init()
  }

  init () {
    let _this = this

    _this.initFirstScreen()
    _this.initNewsFixed()
  }

  initFirstScreen () {
    this.firstTween
    .fromTo(`header`, .7, {y: -20, opacity: 0}, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .15)
    .fromTo(`.breadcrumbs`, .5, {x: -120, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .15)
    .fromTo(`.head_content > h1`, .8, {x: -120, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .35)
    .fromTo(`.head_content > img`, 1, {x: 120, y: -100, opacity: 0}, {
      x: 0,
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .45)
    .fromTo(`.article__date`, .8, {x: -120, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .35)
    .fromTo(`#social_side_links`, .8, {x: 120, opacity: 0}, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .35)
  }

  initNewsFixed () {
    let datePosition = $('.article__date').offset()
    let socialPosition = $('#social_side_links').offset()

    $(window).scroll(function () {
      if ($(window).scrollTop() > (datePosition.top - 115)) {
        $('.article__date').css('position', 'fixed').css('top', '105px')
      } else {
        $('.article__date').css('position', 'absolute').css('top', '675px')
      }

      if ($(window).scrollTop() > (socialPosition.top - 115)) {
        $('#social_side_links').css('position', 'fixed').css('top', '105px')
      } else {
        $('#social_side_links').css('position', 'absolute').css('top', '675px')
      }

      let offset = $('.cta_block').offset().top - 200

      if ($(window).scrollTop() > offset) {
        $('.article__date').css('position', 'absolute').css('top', `${offset}px`)
        $('#social_side_links').css('position', 'absolute').css('top', `${offset}px`)
      }

    })
  }
}



