import { Linear, Power1, TimelineMax, TweenLite } from 'gsap';
import { $window } from '../modules/dev/_helpers';

export default class News {

  constructor () {
    this.section = 'section';

    this.firstScreen = '.head_blog_screen';
    this.firstTween = new TimelineMax({ delay: .9 });

    this.init();
    this.initFirstScreenParallax();
    this.initMoreNews();
  }

  init () {
    let _this = this;

    _this.initFirstScreen();
  }

  initFirstScreen () {
    this.firstTween
    .fromTo(`header`, .7, { y: -20, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .15)
    .fromTo(`.breadcrumbs`, .7, { x: -120, opacity: 0 }, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .15)
    .fromTo(`.title`, .7, { x: -120, opacity: 0 }, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, .15)
    .staggerFromTo('.categories > .item', 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.2, .8)
    .staggerFromTo('.bg > div', 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.2, 1)
    .staggerFromTo('.bg > svg', 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.2, 1.2)
    .staggerFromTo('.news_item', 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.2, 1.4)
    .fromTo(`.line__item > .btn`, .7, { y: -50, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.5);
  }

  initFirstScreenParallax () {
    let $parallaxItems = $('.bg').children();

    let _this = this;

    $window.on('mousemove', (event) => {
      let pageX = event.pageX - ($window.width() * 0.5);
      let pageY = event.pageY - ($window.height() * 0.5);

      $parallaxItems.each(function () {
        let item = $(this);

        if (item.hasClass('news__icon')) {
          _this.setParallax(item, pageX, 50, pageY, -55);
        } else if (item.hasClass('heart__icon')) {
          _this.setParallax(item, pageX, -65, pageY, 85);
        } else {
          _this.setParallax(item, pageX, 50, pageY, 75);
        }
      });
    });
  }

  setParallax (item, pageX, speedX, pageY, speedY) {
    TweenLite.to(item, 0.5, {
      x: (item.position().left + pageX * speedX) * 0.0008,
      y: (item.position().top + pageY * speedY) * 0.0008,
      ease: Linear.ease
    });
  }

  initMoreNews () {
    let $button = $('.load__more');

    $button.click(function () {
      let $categoryInput = $('.selected__category');
      let category = null;

      if ($categoryInput.length) {
        category = $categoryInput.val();
      }

      let offset = $('.news_item').length;

      $.get(`/get-more-news?offset=${offset}&category=${category}`, function (response) {
        let items = JSON.parse(response).replace;
        let $items = $(items);

        if ($items.length < 6) {
          $button.remove();
        }

        $('.blog:eq(1)').append(items);
      });
    });
  }
}



