import { Linear, Power1, TimelineMax, TweenLite, } from 'gsap';
import { $document, $window, css, isScrolledIntoView } from '../modules/dev/_helpers';

export default class Solutions {
  constructor () {
    this.firstTween = new TimelineMax();
    this.firstScreen = '.solutions__';
    this.section = 'section';

    this.secondScreen = '.key_features';
    this.secondTween = new TimelineMax().set([`${this.secondScreen}`], { opacity: 0 });

    this.contetItems = '.key_features  .features_item';

    this.init();
  }

  init () {
    let _this = this;

    this.initFirstScreen();
    this.initWords();
    this.initFirstScreen();

    $window.on('scroll', function (e) {
      $(_this.section).each(function () {
        let index = $(_this.section).index(this);

        if (isScrolledIntoView($(_this.secondScreen))) {
          let index = $(_this.section).index(this);
          if (isScrolledIntoView($(this)) && index === 1) {
            _this.initSecondScreen();
          }
        }
      });
    });

    let $screen = $(`.solutions__`);
    let $parallaxItems = $screen.find('.parallax');

    let _thus = this;

    $screen.on('mousemove', (event) => {
      let pageX = event.pageX - ($screen.width() * 0.5);
      let pageY = event.pageY - ($screen.height() * 0.5);

      $parallaxItems.each(function () {
        let item = $(this);
        _thus.setParallax(item, pageX, 50, pageY, 75);
      });
    });
  }

  initFirstScreen () {
    let _this = this;
    this.firstTween
    .fromTo(`header`, .7, { y: -120, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.5)
    .fromTo(`${this.firstScreen} h3`, .7, { y: -20, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.95)
    .fromTo(`${this.firstScreen} .solutions__item:eq(0) .sub_title`, .7, { y: 20, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 2)
    .fromTo(`${this.firstScreen} .solutions__item:eq(0) .description`, .7, { y: 20, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 2.1)
    .fromTo(`${this.firstScreen} .solutions__item:eq(0) img`, .7, { x: 120, opacity: 0 }, {
      x: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 2.1);

    $window.on('scroll', function (e) {
      $(`.solutions__item`).each(function () {
        let index = $('.solutions__item').index(this);
        if (index === 0) return;

        let selector = `.solutions__item:eq(${index})`;
        let $item = $(selector);
        if (isScrolledIntoView($item) && !$item.hasClass(css.finished)) {
          $item.addClass(css.finished);
          let timeLine = new TimelineMax();
          timeLine.fromTo(`${selector} .sub_title`, .7, { y: 20, opacity: 0 }, {
            y: 0,
            opacity: 1,
            ease: Power1.easeOut,
          }, .5)
          .fromTo(`${selector} .description`, .7, { y: 20, opacity: 0 }, {
            y: 0,
            opacity: 1,
            ease: Power1.easeOut,
          }, .7);

          if ($item.hasClass('reverse')) {
            timeLine.fromTo(`${selector} img`, .7, { x: -120, opacity: 0 }, {
              x: 0,
              opacity: 1,
              ease: Power1.easeOut,
            }, .9);
          } else {
            timeLine.fromTo(`${selector} img`, .7, { x: 120, opacity: 0 }, {
              x: 0,
              opacity: 1,
              ease: Power1.easeOut,
            }, .9);
          }
        }
      });
    });
  }

  initWords () {
    let $headTitle = $('.head__title');
    let sliderSpan = $headTitle.find('p:eq(1)');

    let $childrens = sliderSpan.children();

    $childrens.each(function (index) {
      let $this = $(this);

      if (!index) {
        $this.addClass('is-showing').addClass('show-on-load');
      } else {
        $this.addClass('is-hiding').addClass('hide-on-load');
      }
    });

    $document.ready(function (e) {
      let t = 250, n = 2500, r = n - 3e3;

      function o(e) {return e.is(':last-child') ? e.parent().children().eq(0) : e.next();}

      setTimeout(function () {
        let i = $('.head__title');
        let a = n;

        i.each(function () {
          let i = e(this), s = i.find('.is-showing').outerWidth(), c = t - 100;
          setTimeout(function () {
            i.find('p:eq(1)').css('width', s);
          }, c),
            setTimeout(function () {
              i.find('p:eq(1)').addClass('is-loading');
            }, r),
            setTimeout(function () {
              !function e(t) {
                let i, a, s = o(t), c = o(t).outerWidth();
                t.parents('p').removeClass('is-loading'), a = s, (i = t).hasClass('show-on-load') && i.removeClass('show-on-load'), a.hasClass('hide-on-load') && a.removeClass('hide-on-load'), i.removeClass('is-showing').addClass('is-hiding'), a.removeClass('is-hiding').addClass('is-showing'),
                  setTimeout(function () {e(s);}, n),
                  setTimeout(function () {t.parents('p').addClass('is-loading');}, r), t.parents('p').css('width', c);
              }(i.find('.is-showing').eq(0));
            }, a);
        });
      }, 2000);
    });
  }

  initSecondScreen () {
    if ($(`${this.secondScreen}`).hasClass(css.finished) || this.secondTween.isActive()) return;

    this.secondTween
    .to(`${this.secondScreen}`, .8, { opacity: 1, ease: Power1.easeOut }, .5)
    .fromTo(`${this.secondScreen} h4`, .7, { y: -20, opacity: 0 }, {
      y: 0,
      opacity: 1,
      ease: Power1.easeOut,
    }, 1.4)
    .staggerFromTo(this.contetItems, 0.65, {
      opacity: 0,
      y: -20,
    }, {
      opacity: 1,
      y: 0,
      ease: Power1.easeOut,
    }, 0.2, 1.8)
    .addCallback(() => $(this.secondScreen).addClass(css.finished));

  }

  setParallax (item, pageX, speedX, pageY, speedY) {
    TweenLite.to(item, 0.5, {
      x: (item.position().left + pageX * speedX) * 0.0008,
      y: (item.position().top + pageY * speedY) * 0.0008,
      ease: Linear.ease
    });
  }
}



